$prefix: '.rise-blocks';
@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";

#{$prefix}-navigation-menu-wrapper{
    width:  100%;
    .rise-blocks-main-nav,
    .widget_nav_menu ul.menu  {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        list-style: none;
        margin: 0;
        padding: 0;
        width: 100%;
        box-sizing: border-box !important;

        li {
            margin-right: 0;
            font-size: 16px;
            position: relative;
            list-style: none;
            a {
                color: #6e6e6e;
                transition: all .3s;
                text-decoration:  none;
                display: inline-block;
                width: 100%;
                &:hover{                    
                    color: #000;
                }
            }

            &.current-menu-item {
                a {
                    color: #000;

                }
            }

            &:last-child {
                margin-right: 0;
            }

            .sub-menu {
                position: absolute;
                list-style: none;
                margin: 0;
                width: 220px;
                left: 0;
                padding: 15px 0 0;
                top: 190%;
                border-radius: 5px;
                transition: all .3s;
                opacity: 0;
                z-index: -1;
                visibility: hidden;
                border-radius: 0;

                    li {
                    margin-right: 0;                        
                    
                    transition: all .3s;

                    a {
                        color: #4e4c4c;
                        padding: 7px 15px;
                    }

                   
                    }

                    .sub-menu {
                        top: 0 !important;
                        left: 100%;
                    }
            }

            &:hover,
            &:focus-within {
                > .sub-menu {
                    opacity: 1;
                    visibility: visible;
                    z-index: 999;
                    top: 100%;
                }
            }
        }

        > li{
            margin-right: 5px;
            &:last-child{
                margin-right: 0;
            }
        }

        > li.menu-item-has-children {
            > a {
                &:after {
                    content: "\F107";
                    font-family: "FontAwesome";
                    margin-left: 8px;
                    font-weight: 600;
                }
                position: relative;
            }
        } 
    }
}





.align-center {
    .rise-blocks-main-nav,
    .widget_nav_menu ul.menu {
        justify-content: center;
    }
}

.align-right {
    .rise-blocks-main-nav,
    .widget_nav_menu ul.menu {
        justify-content: flex-end;
    }
}

.align-left {
    .rise-blocks-main-nav,
    .widget_nav_menu ul.menu {
        justify-content: flex-start;
    }
}


.hc-offcanvas-nav {
    .nav-container {
        ul.rise-blocks-main-nav.menu {
            display: block;

            a {
                display: block;
            }
        }
    }

     li.nav-parent .nav-item-link:not(:last-child) {
        width: calc(100% - 45px);
    }
}

.rise-blocks-header {
    position: relative;

    .hc-nav-trigger {
        right: 0;
        z-index: 999;
        top: 50%;
        transform: translate(0, -50%);
    }
}

body .hc-offcanvas-nav .nav-content{
    opacity: 1;
}
body .hc-offcanvas-nav .nav-item-link, .hc-offcanvas-nav li.nav-close a, 
body .hc-offcanvas-nav .nav-back a {
    border-color: transparent;
}

body .hc-offcanvas-nav a.nav-next {
    border-left: none;
    border-bottom: none;
}

body .hc-offcanvas-nav .nav-content>.nav-close:first-child a, 
body .hc-offcanvas-nav .nav-title+.nav-close a.has-label, 
body .hc-offcanvas-nav li.nav-close a, 
body .hc-offcanvas-nav .nav-back a,
body .hc-offcanvas-nav.nav-levels-expand li.level-open>.nav-item-wrapper>a, 
body .hc-offcanvas-nav.nav-levels-none li.level-open>.nav-item-wrapper>a,
body .hc-offcanvas-nav.nav-levels-expand li.level-open>.nav-item-wrapper>a:hover, 
body .hc-offcanvas-nav.nav-levels-none li.level-open>.nav-item-wrapper>a:hover {
    background: transparent;
    border-top: transparent;
    border-bottom: transparent;
}

body .hc-offcanvas-nav .nav-wrapper>.nav-content>ul:first-of-type>li:first-child:not(.nav-back):not(.nav-close)>.nav-item-wrapper>.nav-item-link {
    border-top: none;
}

body .hc-offcanvas-nav .nav-close-button span::before {
    margin-left: -15px;
    transform: rotate(135deg);
}

body .hc-offcanvas-nav .nav-close-button span::before, 
body .hc-offcanvas-nav .nav-close-button span::after {
    width: 12px;
    height: 12px;
}

a.nav-next {
    background: transparent !important;
    transition: none !important;
}